IG$.m$CompMatrix = function()
{
	var me = this;
	
	/**
	 * One attribute of the data set captured by this ComparisonItem.
	 */
	me.xField = null;
	me.xFieldName = null;
	
	/**
	 * The other attribute of the data set captured by this ComparisonItem.
	 */
	me.yField = null;
	me.yFieldName = null;
	
	/**
	 * The numerical relationship between the xField and the yField. When used in the
	 * default implementation of the ComparisonMatrix, this value is the correlation
	 * coefficent.
	 */
	me.compVal;
	
	/**
	 * The dataProvider of the ComparisonMatrix that created this ComparisonItem.
	 */
	me.dataSet = null;
	
	me.toString = function()
	{
		return me.xFieldName + ", " + me.yFieldName + " : " + me.compVal;
	}
}

IG$.compMatrixPlotCell = function()
{
	var me = this,
		i,
		o,
		browser = window.bowser,
		hasSVG = (window && window.SVGAngle) || document.implementation.hasFeature("http://www.w3.org/TR/SVG11/feature#BasicStructure", "1.1");
		
	me.hasCanvas = (!hasSVG && !browser.msie) ? true : false;
	
	me.compItem;
	me.xMin;
	me.xMax;
	me.yMin;
	me.yMax;
	me.xRange;
	me.yRange;
	
	me.cr1 = 0;
	me.cr2 = 0;
	
	me.selected;
	
	me.width;
	me.height;
	
	me.html = $('<div class="matrixchart"></div>');
	
	if (me.hasCanvas == true)
	{
		me.canvas = $('<canvas></canvas>');
		me.canvas.appendTo(me.html);
	}
	
	me.updateExtremes = function()
	{
		if(me.compItem && me.compItem.dataSet)
		{
			me.xMin = Number.MAX_VALUE;
			me.yMin = Number.MAX_VALUE;
			me.xMax = Number.MIN_VALUE;
			me.yMax = Number.MIN_VALUE;
			for (i=0; i < me.compItem.dataSet.length; i++)
			{
				o = me.compItem.dataSet[i];
				
				me.xMin = Math.min(me.xMin,o[me.compItem.xField]);
				me.yMin = Math.min(me.yMin,o[me.compItem.yField]);
				me.xMax = Math.max(me.xMax,o[me.compItem.xField]);
				me.yMax = Math.max(me.yMax,o[me.compItem.yField]);
			}
			me.xRange = me.xMax - me.xMin;
			me.yRange = me.yMax - me.yMin;
		}
	}
	
	me.computeX = function(o) {
		var xFieldValue = o[me.compItem.xField],
			xPercent = 1 - (me.xMax - xFieldValue) / me.xRange;
		return xPercent;
	}
	
	me.computeY = function(o) {
		var yFieldValue = o[me.compItem.yField],
			yPercent = 1 - (me.yMax - yFieldValue) / me.yRange;
		return yPercent;
	}
	
	me.updatePlot = function() {
		var i,
			o,
			plotPointX,
			plotPointY;
		
		me.plotData = [];
		
		for (i=0; i < me.compItem.dataSet.length; i++)
		{
			o = me.compItem.dataSet[i];
			plotPointX = me.computeX(o);
			plotPointY = me.computeY(o);

			me.plotData.push([plotPointX, plotPointY]);
		}
		
		me.drawPlot();
	}
	
	me.drawPlot = function() {
		var me = this;
		
		me.width = me.html.innerWidth();
		me.height = me.html.innerHeight();
		
		var gap = 3,
			color,
			dcache = {};
			
		color = me.compItem.compVal > me.cr2 ? "rgba(0,0,255," : 
				me.compItem.compVal > me.cr1 ? "rgba(150,150,150," :
				"rgba(255,0,0,";
		
		if (me.hasCanvas == true)
		{
			me.canvas.width = me.width;
			me.canvas.height = me.height;
			
			me.ctx = me.canvas[0].getContext('2d');
			me.ctx.scale(1, 1);
			me.ctx.fillStyle = "rgb(255,255,255)";
			me.ctx.fillRect (0, 0, me.width, me.height);
			me.ctx.strokeStyle = "#5F5F5F";
			me.ctx.lineWidth = 1;
			
			me.ctx.fillStyle = color + '.5)';
		}
		else
		{
			if (!me.paper)
			{
				me.paper = Raphael(me.html[0], 0, 0, me.width, me.height);
			}
			else
			{
				me.paper.clear();
			}
		}
		
		for (i=0; i < me.plotData.length; i++)
		{
			var o = me.plotData[i],
				cx = (isNaN(o[0]) == false ? o[0] * me.width : 0),
				cy = (isNaN(o[1]) == false ? (1-o[1]) * me.height : me.height),
				tx = Math.floor(cx),
				ty = Math.floor(cy),
				key,
				radius = 2;
			
			tx = tx - tx % 5;
			ty = ty - ty % 5;
			
			key = '' + tx + ',' + ty;
			
			if (!dcache[key])
			{
				if (me.hasCanvas == true)
				{
					me.ctx.beginPath();
					me.ctx.arc(cx, cy, 1, 0, Math.PI*2, true);
					me.ctx.closePath();
					me.ctx.fill();
					dcache[key] = {obj: true, mbody: 0};
				}
				else
				{
					dcache[key] = {obj: me.paper.circle(cx, cy, radius), mbody: 0};
					dcache[key].obj.attr("fill", color + '0.5)');
				}
			}
		}
		
		dcache = null;
	}
	
	me.updateDispList = function(w, h) {
		me.drawPlot();
	}

	me.commitProps = function() {
		me.updateExtremes();
		me.updatePlot();
	}
}

IG$.ComparisonMatrix = function(parent, config)
{
	var me = this,
		detailviewer,
		labelhtml;
	
	me.compItems = null;
	me.cells = [];
	me.parent = parent;
	me.config = config;
	me.textFields = [];
	me.showLabels = config.showLabels || true;
	
	me.cr1 = (config.cr1 == null || typeof config.cr1 == 'undefined') ? 0 : config.cr1;
	me.cr2 = (config.cr2 == null || typeof config.cr2 == 'undefined') ? 0 : config.cr2;
	
	me.container = $('<div class="matrixcontainer"></div>');
	me.container.appendTo(me.parent);
	
	labelhtml = me.labelhtml = $('<div class="matrixlabel"></div>');
	labelhtml.css({position: 'absolute', top: '0px', left: '0px', width: '0px', height: '0px'});
	labelhtml.appendTo(me.parent);
	me.isDirty = false;
	
	detailviewer = me.detailviewer = $('<div class="detailviewer"></div>');
	detailviewer.css({
			position: 'absolute',
			top: 10,
			left: 10,
			width: 100,
			height: 100,
			margin: 0,
			padding: 0,
			backgroundColor: '#ececec',
			borderColor: '#222222',
			display: 'none'
		}
	);
	detailviewer.appendTo(me.parent);
	detailviewer.bind('click', function() {
		$(this).fadeOut();
	});
	
	me.detailcontainer = $('<div class="detailmatrixplot"></div>');
	me.detailcontainer.css({
			position: 'absolute',
			margin: '20 0 0 0',
			padding: 0,
			top: 0, left: 0, right: 0, bottom: 0
		}
	);
	
	me.uid = 0;
	
	me.detailcontainer.appendTo(detailviewer);
	
	var closebtn = $('<div class="detailmatrixclosebtn"></div>');

	closebtn.css({
		position: 'absolute',
		top: 2, width: 16, height: 16, right: 2
	});
	detailviewer.append(closebtn);
	closebtn.bind({
		click: function() {
			detailviewer.fadeOut();
		}
	});
	
	/**
	 * Evaluates the correlation coefficent between the xField and yField properties
	 * on the Objects in the collection.
	 */
	me.correlationCoefficient = function(collection, xField, yField)
	{
		var len = collection.length,
			xTotal = 0,
			yTotal = 0,
			xSquaredTotal = 0,
			ySquaredTotal = 0,
			xYTotal = 0,
			o,
			i;
			
		for (i=0; i < collection.length; i++)
		{
			o = collection[i];
			var xValue = o[xField];
			var yValue = o[yField];
			xTotal += xValue;
			yTotal += yValue;
			xSquaredTotal += Math.pow(xValue,2);
			ySquaredTotal += Math.pow(yValue,2);
			xYTotal += xValue * yValue;
		}
		var top = (len * xYTotal) - (xTotal * yTotal);
		var bottomLeft = Math.sqrt( (len * xSquaredTotal) - Math.pow(xTotal,2) );
		var bottomRight = Math.sqrt( (len * ySquaredTotal) - Math.pow(yTotal,2) );
		return top/(bottomLeft * bottomRight);
	}
	
	me.comparisonFunction = me.correlationCoefficient;

	/**
	 * Evaluate the relationship between each pair of attributes in the fields Array
	 * using the comparisonFunction. 
	 */
	me.generateComparisonStatistics = function()
	{
		me.compItems = [];
		
		if(me.fields.length == 0 || me.dataSet == null || me.dataSet.length == 0)
			return;
		
		var len = me.fields.length;
		var index = 0;
		for (var a = 0; a < len; a++)
		{
			for (var b = 0; b < a + 1;  b++)
			{
				if(a != b)
				{
					var item = new IG$.m$CompMatrix();
					item.xFieldName = me.fields[a];
					item.xField = a;
					item.yFieldName = me.fields[b];
					item.yField = b;
					item.compVal = me.comparisonFunction.call(me, me.dataSet, item.xField, item.yField);
					me.compItems.push(item);
					index++;
				}
			}
		}
	}
	
	me.createOrRemoveCells = function() {
		var me = this,
			len = me.compItems.length, cid,
			ncells = [];
		
		me.matrixitems = {};
		
		while(me.cells.length < len)
		{
			var cellToAdd = new IG$.compMatrixPlotCell();
			cellToAdd.html.css({border: '1px solid #a5a5a5', width: 200, height: 200, position: 'absolute', padding: 0});
			// cellToAdd.addEventListener(MouseEvent.CLICK, handleCellClick, false, 0, true);
			cellToAdd.html.appendTo(me.container);
			cid = 'matrixplot' + me.uid;
			cellToAdd.html[0].id = cid;
			cellToAdd.id = cid;
			me.uid++;
			
			ncells.push(cellToAdd);
			me.cells.push(cellToAdd);
			me.matrixitems[cid] = cellToAdd;
		}
		
		$.each(ncells, function(i, cellToAdd) {
			cellToAdd.html.bind({
				click: function() {
					me.drawDetailPlot.call(me, cellToAdd);
				}
			});
		});
		
		while(len < me.cells.length)
		{
			var cellToRemove = me.cells.pop();
			cellToRemove.remove();
		}
	}
	
	me.updateTextFields = function() {
		var me = this,
			len = me.fields.length,
			a,
			textField,
			textFieldToAdd;
		
		for(a = 0; a < len; a++)
		{
			if(me.textFields.length < a + 1)
			{
				textFieldToAdd = $('<div class="matrixtextfield"></div>');
				textFieldToAdd.css({fontSize: '0.8em', padding: '0 0 0 3', margin: 0, position: 'absolute', width: 130});
				me.textFields.push(textFieldToAdd);
				textFieldToAdd.appendTo(me.labelhtml);
			}
			textField = me.textFields[a];
			textField[0].innerText = me.fields[a];
			
			// var metrics:TextLineMetrics = textField.getLineMetrics(0);
			// textField.width = metrics.width + 4;
			// textField.height = metrics.height + 2;
		}
		while(len < me.textFields.length)
		{
			var textFieldToRemove = textFields.pop();
			me.textFieldToRemove.remove();
		}
	}
	
	/**
	 * Position the textFields along the edges of the matrix.
	 */
	me.layoutTextFields = function() {
		var me = this,
			len = me.textFields.length,
			firstTextField,
			startY,
			a,
			textField,
			px,
			py;
		if(len > 0)
		{
			firstTextField = me.textFields[0];
			startY = firstTextField.height();
			
			for(a = 0; a < len; a++)
			{
				textField = me.textFields[a];
				px = a * me.actualCellSize + 1;
				if(a == 0)
				{
					py = 0;
				}
				else
				{
					py = (a - 1) * me.actualCellSize + startY;
				}
				textField.css({left: px, top: py}); 
				// textField.visible = true;
				
			}
		}
	}

	/**
	 * Arrange the cells in a stair-step fashion.
	 */
	me.layoutCells = function()
	{
		var me = this,
			startY = 0;
		if(me.showLabels && me.textFields.length > 0)
		{
			var firstTextField = me.textFields[0];
			startY = firstTextField.position().top + firstTextField.height();
		}
		
		var len = me.fields.length;
		var index = 0;
		for(var a = 0; a < len; a++)
		{
			for(var b = 0; b < a + 1;  b++)
			{
				if(a != b)
				{
					var item = me.compItems[index];
					
					var cell = me.cells[index];
					item.dataSet = me.dataSet;
					cell.compItem = item;
					cell.selected = (item == me.selectedItem);
					
					// toolTip isn't a part of IUIComponent, interesting, eh?
					var tt = "";
					tt = item.xField + "\n" + item.yField + "\n" + item.compVal;
					cell.toolTip = tt;
					
					var cellX = b * me.actualCellSize;
					var cellY = (a - 1) * me.actualCellSize + startY;
					cell.html.css({left: cellX, top: cellY, width: me.actualCellSize, height: me.actualCellSize});
					
					cell.cr1 = me.cr1;
					cell.cr2 = me.cr2;
					
					if (me.isDirty == true)
					{
						cell.commitProps.call(cell);
					}
					else
					{
						cell.updateDispList.call(cell);
					}
					
					index++;
				}
			}
		}
	}
	 
	me.computeCellSize = function(width, height)
	{
		var me = this,
			availableWidthForCells = width,
			availableHeightForCells = height;
		
		if(me.showLabels == true && me.textFields.length > 0)
		{
			availableWidthForCells = width - me.textFields[me.textFields.length - 1].width();
			availableHeightForCells = height - me.textFields[0].height();
		}
		else
		{
			availableWidthForCells = width;
			availableHeightForCells = height;
		}
		var measuredCellSize = Math.min(availableWidthForCells,availableHeightForCells) / (me.fields.length - 1);
		return measuredCellSize;
	}
	
	me.updateDispList = function() {
		var me = this,
			p = $(me.parent),
			w = p.innerWidth(),
			h = p.innerHeight();
			
		me.container.css({width: w, height: h});
		
		me.actualCellSize = me.computeCellSize(w, h);
		if (me.showLabels && me.textFields.length > 0)
		{
			me.layoutTextFields();
		}
		me.layoutCells();
	}
	
	me.commitProp = function() {
		var me = this;
		
		me.isDirty = true;
		me.generateComparisonStatistics();
		me.createOrRemoveCells();
		me.updateTextFields();
		me.updateDispList();
		me.isDirty = false;
	}	
}

IG$.ComparisonMatrix.prototype.drawDetailPlot = function(matrixowner) {
	var me = this,
		matrix = me.matrixitems[matrixowner.id],
		color = matrix.compItem.compVal > me.cr2 ? "rgba(0,0,255,.5)" : 
				matrix.compItem.compVal > me.cr1 ? "rgba(150,150,150,.5)" :
				"rgba(255,0,0,0.5)",
		plotoption;
	
	if (matrix)
	{
		if (window.Highcharts)
		{
			plotoption = {
				chart: {
					 renderTo: me.detailcontainer[0], 
					 defaultSeriesType: 'scatter',
					 zoomType: 'xy'
				},
				title: {
					 text: matrix.compItem.xFieldName + " vs. " + matrix.compItem.yFieldName
				},
				xAxis: {
					 title: {
						enabled: true,
						text: matrix.compItem.xFieldName
					 },
					 startOnTick: true,
					 endOnTick: true,
					 showLastLabel: true,
					 min: 0,
					 max: 1
				},
				yAxis: {
					 title: {
						text: matrix.compItem.yFieldName
					 },
					 min: 0,
					 max: 1
				},
				tooltip: {
					 formatter: function() {
						   return ''+
					   me.x +', '+ me.y +'';
					 }
				},
				legend: {
					enabled: false,
					 layout: 'vertical',
					 align: 'left',
					 verticalAlign: 'top',
					 floating: true,
					 backgroundColor: '#FFFFFF',
					 borderWidth: 1
				},
				plotOptions: {
					scatter: {
						marker: {
							   radius: 5,
							   states: {
								  hover: {
									 enabled: true,
									 lineColor: 'rgb(100,100,100)'
								  }
							   }
						},
						states: {
							   hover: {
								  marker: {
									 enabled: false
								  }
							   }
						}
					}
				},
				
				series: [{
					 name: 'comparison',
					 color: color,
					 data: matrix.plotData
				}]
			};
		}
		else
		{
			plotoption = {
				chart: {
					 renderTo: me.detailcontainer[0], 
					 defaultSeriesType: 'scatter',
					 zoomType: 'xy',
					 echart_theme: me.config.echart_theme
				},
				title: {
					 text: matrix.compItem.xFieldName + " vs. " + matrix.compItem.yFieldName
				},
				xAxis: {
					 title: {
						enabled: true,
						text: matrix.compItem.xFieldName
					 },
					 startOnTick: true,
					 endOnTick: true,
					 showLastLabel: true,
					 min: 0,
					 max: 1
				},
				yAxis: {
					 title: {
						text: matrix.compItem.yFieldName
					 },
					 min: 0,
					 max: 1
				},
				tooltip: {
					 formatter: function() {
						   return ''+
					   me.x +', '+ me.y +'';
					 }
				},
				legend: {
					enabled: false,
					 layout: 'vertical',
					 align: 'left',
					 verticalAlign: 'top',
					 floating: true,
					 backgroundColor: '#FFFFFF',
					 borderWidth: 1
				},
				plotOptions: {
					scatter: {
						marker: {
							   radius: 5,
							   states: {
								  hover: {
									 enabled: true,
									 lineColor: 'rgb(100,100,100)'
								  }
							   }
						},
						states: {
							   hover: {
								  marker: {
									 enabled: false
								  }
							   }
						}
					}
				},
				
				series: [{
					type: "scatter",
					symbolSize: 5,
					name: 'comparison',
					color: color,
					data: matrix.plotData
				}]
			};
		}
	
		// me.detailviewer.css({width: 400, height: 300});
		var parent = $(me.parent),
			pw = parent.width(),
			ph = parent.height(),
			vs = Math.min(ph, pw) * 0.85;
			
		me.detailviewer.css({left: (pw - vs - 10), top: 5, width: vs, height: vs});
		me.detailviewer.show();
		
		if (window.Highcharts)
		{
			me.plotdetail = new Highcharts.Chart(plotoption);
		}
		else
		{
			me.plotdetail = echarts.init(plotoption.chart.renderTo, plotoption.chart.echart_theme || ig$.echarts_theme || 'amplix', {
				renderer: "canvas" // "svg"
			});
			
			me.plotdetail.setOption(plotoption);
		}
	}
}